body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  margin: 0;
  padding: 0;
}

body {
  background-image: linear-gradient(to right,#d1fdf7,#31ffa9);
}

.App {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas: 
  ". . . ."
  "title title title title"
  ". . . ."
  ". instructions instructions ."
  ". search search ."
  ". generate generate ."
  "display display display display"
  "display display display display";
  grid-gap: 20px;
}

.title {
  grid-area: title;
  justify-self: center;
  font-size: 50px;
  font-family: 'Roboto', sans-serif;
}

.search {
  grid-area: search;
  justify-self: center;
}

.generate {
  grid-area: generate;
  justify-self: center;
}

.display {
  grid-area: display;
  justify-items: center;
}

#instructions {
  grid-area: instructions;
  justify-self: center;
}


#search-field {
  font-size: 20px;
  padding: 10px;
  margin-top: 20px;
  margin-right: 15px;
}

#search-btn {
  grid-area: btn;
  font-size: 20px;
  border-radius: 15px;
  border: none;
  padding: 10px;
  margin-top: 20px;
  background-color: #2c2c2c;
  color: #fff;
}
.generate {
  display: inline;
  justify-self: center;
}

.gen-btn {
  margin-top: 20px;
  margin-left: 25px;
  margin-right: 25px;
  font-size: 20px;
  padding: 10px;
  border-radius: 15px;
  border: 0px;
  background-color: #2c2c2c;
  color: #fff;
}
.display {
  float: left;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.display img {
  margin: 10px;
  border-radius: 15px;
}
